<template>
  <transition name="slide">
    <div class="user-center">
      <div class="back" @click="back">
        <i class="icon-back"></i>
      </div>
      <div class="switches-wrapper">
        <m-switches @switch="switchItem" :currentIndex="currentIndex" :switches="switches"></m-switches>
      </div>
      <div class="play-btn" @click.stop.prevent="random">
        <i class="icon-play"></i>
        <span class="text">随机播放全部</span>
      </div>
      <div class="list-wrapper" ref="listWrapper">
        <m-scroll ref="favoriteList" class="list-scroll" v-if="currentIndex===0" :data="favoriteList">
          <div class="list-inner">
            <m-song-list :songs="favoriteList" @select="selectSong"></m-song-list>
          </div>
        </m-scroll>
        <m-scroll ref="playHistory" class="list-scroll" v-if="currentIndex===1" :data="playHistory">
          <div class="list-inner">
            <m-song-list :songs="playHistory" @select="selectSong"></m-song-list>
          </div>
        </m-scroll>
      </div>
      <div class="no-result-wrapper" v-show="noResult">
        <m-no-result :title="getNoResultDesc"></m-no-result>
      </div>
    </div>
  </transition>
</template>

<script>
  import switches from "@/base/switchs/switchs"
  import {mapGetters, mapActions} from "vuex"
  import Scroll from "@/base/scroll/scroll"
  import SongList from "@/base/song-list/song-list"
  import Song from "@/common/js/Song"
  import {playlistMixin} from "@/common/js/mixin"
  import NoResult from "@/base/no-result/no-result"

  export default {
    data() {
      return {
        currentIndex: 0,
        switches: [
          {name: '我喜欢的'},
          {name: '最近听的'}
        ]
      }
    },
    mixins: [playlistMixin],
    components: {
      "m-switches": switches,
      "m-scroll": Scroll,
      "m-song-list": SongList,
      "m-no-result": NoResult
    },
    computed: {
      ...mapGetters(['playHistory', 'favoriteList', 'currentSong']),
      noResult() {
        return this.currentIndex === 0 ? !this.favoriteList.length : !this.playHistory.length;
      },
      getNoResultDesc() {
        return this.currentIndex === 0 ? '暂无收藏歌曲' : '你还没有听过歌曲';
      }
    },
    methods: {
      ...mapActions(['insertSong', 'radomPlay']),
      handlerPlaylist(playList) {
        let list = this.currentIndex === 0 ? this.favoriteList : this.playHistory;
        if (list.length > 0) {
          const bottom = playList.length > 0 ? '60px' : "";
          this.$refs.listWrapper.style.bottom = bottom;
          this.$refs.playHistory && this.$refs.playHistory.refresh();
          this.$refs.favoriteList && this.$refs.favoriteList.refresh();
        }
      },
      back() {
        this.$router.back();
      },
      random() {
        let list = this.currentIndex === 0 ? this.favoriteList : this.playHistory;
        if (!(list.length > 0)) return;
        list=list.map((song) => {
          return new Song(song);
        });
        this.radomPlay({list});
      },
      selectSong(song) {
        if (this.currentSong.id === song.id) return;
        this.insertSong(new Song(song));
      },
      switchItem(index) {
        this.currentIndex = index;
      }
    }
  }

</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~@/common/stylus/variable"

  .user-center
    position: fixed
    top: 0
    bottom: 0
    z-index: 100
    width: 100%
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .switches-wrapper
      margin: 10px 0 30px 0
    .play-btn
      box-sizing: border-box
      width: 135px
      padding: 7px 0
      margin: 0 auto
      text-align: center
      border: 1px solid  $color-text-l
      color: $color-text-l
      border-radius: 100px
      font-size: 0
      .icon-play
        display: inline-block
        vertical-align: middle
        margin-right: 6px
        font-size: $font-size-medium-x
      .text
        display: inline-block
        vertical-align: middle
        font-size: $font-size-small
    .list-wrapper
      position: absolute
      top: 110px
      bottom: 0
      width: 100%
      .list-scroll
        height: 100%
        overflow: hidden
        .list-inner
          padding: 20px 30px
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



